@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Radley:ital@0;1&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  line-height: 1;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

picture,
video,
canvas,
svg {
  max-width: 100%;
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

.radley {
  font-family: "Radley", serif;
}

body {
  background-color: #121212;
  color: #e3e3e3;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.flex-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.bottom-bar {
  height: 40px;
  background-color: #171717;
  border-top: 1px solid #272727;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-content {
  flex: 1;
  display: flex;
  align-items: stretch;
  justify-content: center;
}

.main {
  flex: 1;
}

.sidebar {
  width: 250px;
  background-color: #171717;
}

.top-menu {
  display: flex;
  height: 80px;
  border-bottom: 1px solid #272727;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}
.top-menu .user-menu {
  display: flex;
  font-size: 14px;
  align-items: center;
  position: relative;
  gap: 5px;
}
.top-menu .user-menu .user-avatar {
  width: 35px;
  height: 35px;
  border-radius: 20px;
  background-color: #272727;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 22px;
  color: #e3e3e3;
  cursor: pointer;
  user-select: none;
}
.top-menu .user-menu .user-sub-menu {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: -214px;
  bottom: -165px;
  width: 250px;
}
.top-menu .user-menu .user-sub-menu.active {
  opacity: 1;
  pointer-events: all;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content {
  background-color: #171717;
  border: 1px solid #272727;
  padding: 10px;
  border-radius: 6px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-status {
  font-size: 10px;
  text-transform: uppercase;
  border: 1px solid #272727;
  padding: 7px 10px 5px 10px;
  border-radius: 4px;
  display: inline-block;
  margin-bottom: 10px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-status.premium {
  color: #e9ff70;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .email {
  font-size: 13px;
  color: #989b99;
  margin-top: 5px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list {
  border-top: 1px solid #272727;
  margin-top: 10px;
  padding-top: 10px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list li {
  padding-top: 5px;
  font-size: 13px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list li:first-child {
  padding-top: 0;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list li a {
  color: #989b99;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list li a:hover {
  color: #e9ff70;
}

.search input {
  font-size: 14px;
  background-color: #272727;
  height: 40px;
  border-radius: 20px;
  border: 0;
  outline: 0;
  padding: 0 20px;
  color: #e3e3e3;
  border: 1px solid #272727;
}
.search input::placeholder {
  color: #989b99;
}

